@import "../../sass/variables.scss";

.homepage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: inherit;

  &::before {
    content: "";
    top: 9rem;
    left: 0;
    height: 4rem;
    width: 100%;
    position: fixed;
    display: block;
    z-index: 3;

    background-image: linear-gradient(
      to bottom,
      $color-black 10%,
      transparent 100%
    );
  }

  &-phrase {
    margin: 20rem 0 15rem 0;
    &--title {
      font-family: "Titillium Web", "Roboto Condensed", daniel, sans-serif;
      font-weight: 700;
    }
  }
}
