@import "./../../sass/variables.scss";
@import "../../sass/animations.scss";
.about {
  display: flex;
  flex-direction: column;
  overflow-y: hidden;

  &::before {
    content: "";
    top: 9rem;
    left: 0;
    height: 4rem;
    width: 100%;
    position: fixed;
    display: block;
    z-index: 3;

    background-image: linear-gradient(
      to bottom,
      $color-black 10%,
      transparent 100%
    );
  }

  &-section {
    &--1 {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 1rem;
      margin-top: 10rem;

      &_title {
        margin: 2rem;
        color: $color-purple;
        font-weight: 700;
        font-size: 5rem;
        text-transform: uppercase;
      }
      &_text {
        text-align: center;
        margin: 0 0 5rem 0;
        font-size: 2.2rem;
      }
    }
    &--container {
      display: flex;
      justify-content: center;

      &_1 {
        flex: 1;
      }
      &_2 {
        flex: 1;
      }
    }
    &--2,
    &--3 {
      text-align: justify;
      background-color: rgba(0, 0, 0, 0.407);
      border-radius: 2rem;
      box-shadow: 10px 5px 5px rgb(0, 0, 0);

      &_title {
        margin: 3rem;
        text-align: center;
        color: $color-purple;
        font-weight: 700;
      }
      &_text {
        margin: 1rem 0;
      }
    }

    &--2 {
      padding: 1rem 5rem 4rem 5rem;
      margin: 0rem 20rem 5rem 10rem;

      &_title {
        // color: $color-white;
      }
      &_text {
      }
    }

    &--2b {
      background-color: rgba(255, 255, 255, 0.118);
      border-radius: 1rem;
      // box-shadow: 10px 5px 5px rgb(0, 0, 0);
      padding: 1rem 1rem 1rem 1rem;
      margin: 0rem 20rem 5rem 10rem;
      display: flex;
      flex-direction: column;
      &_title--1,
      &_title--2 {
        margin: 0.1rem;
        font-family: daniel;
        font-weight: 700;
        letter-spacing: 0.3rem;
      }
      &_title--1 {
        color: $color-purple;
        text-align: start;
      }
      &_title--2 {
        text-align: end;
      }
    }

    &--3 {
      padding: 1rem 5rem 4rem 5rem;
      margin: 0rem 10rem 2rem 20rem;

      &_title {
        color: $color-purple;
        font-weight: 700;
      }
      &_text {
      }
    }
    &--4 {
      // padding: 1rem 5rem 4rem 5rem;
      margin: 0rem 10rem 0rem 10rem;
      display: flex;
      flex-direction: column;

      &_team {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        padding: 0 15rem 0 20rem;
      }

      &_title {
        margin: 3rem 0rem 3rem 7rem;
        text-align: center;
        text-transform: uppercase;
        letter-spacing: 0.3rem;
        font-family: danielbd;
        font-weight: 900;
        font-size: 3.5rem;
      }
      &_person {
        flex: 1;
        display: flex;
        // flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 2rem;

        &__img {
          margin: 0 3rem;
          object-fit: contain;
          background-color: $color-white;
          border-radius: 50%;
          height: 13rem;
          width: 13rem;
          box-shadow: 10px 5px 5px rgb(0, 0, 0);
          border: solid $color-purple 4px;
        }
        &__text {
          animation: moveInLeftStart 1s;
        }
        &__name {
          font-size: 2.5rem;
        }
        &__job {
          font-style: italic;
          font-size: 2rem;
        }
      }
    }
  }
}
