@import "../sass/variables.scss";

.App {
  display: flex;
  flex-direction: column;

  height: auto;
  width: 100vw;
}

//headers
h1 {
  color: $color-white;
  font-family: "Roboto Condensed", daniel, sans-serif;
  font-size: 5rem;
  font-weight: 400;
  position: relative;
}

h2 {
  color: $color-white;
  font-family: "Roboto Condensed", daniel, sans-serif;

  font-size: 2.5rem;
  font-weight: 400;
  position: relative;
}
p {
  color: $color-white;
  font-family: "Titillium Web", "Roboto Condensed", daniel, sans-serif;

  font-size: 1.7rem;
  font-weight: 400;
  position: relative;
}
