@import "./../sass/variables.scss";

.header-page {
  text-decoration: none;
  list-style: none;
  margin: 0 5rem 0 0;
  text-transform: uppercase;
  cursor: pointer;

  &--link {
    &_name {
      &:hover {
        color: $color-purple;
      }
      &--active {
        color: $color-purple;
        opacity: 0.5;
      }
    }
  }
}

a:link {
  text-decoration: none;
}
