@import "./../sass/variables.scss";

.header {
  background-color: $background-header;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 9rem;
  position: fixed;
  width: 100vw;
  z-index: 100;
  &-pages {
    display: flex;
  }

  &-logo {
    margin: 0 2rem;
    position: relative;
    width: auto;

    &--img {
      height: 8rem;
      width: auto;
    }
  }
}
